.list {
  max-height: 420px;
  overflow-y: auto;
  margin: 0 -24px;
  padding: 0 24px;
}
.item {
  display: flex;
  padding: 12px 0;
  .cover {
    width: 72px;
    height: 72px;
    border: 1px solid #e5e5e5;
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
  .info {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-left: 16px;
    .title {
      display: flex;
      align-items: center;
    }
    .actions {
      display: flex;
      justify-content: space-between;
      .action {
        display: flex;
      }
    }
  }
  .label {
    font-size: 14px;
    color: rgba(0,0,0,0.45);
    padding-right: 8px;
  }
  .value {
    flex: 1;
  }
}
