.container {
  height: 100%;
  display: flex;
  flex-direction: column;
  user-select: none;
  .header {
    height: 56px;
    border-bottom: 1px solid rgba(0,0,0,0.06);
  }
  .list {
    flex: 1;
    overflow-y: auto;
    .title {
      color: rgba(0,0,0,0.45);
      font-size: 12px;
      text-align: center;
      margin: 16px 0;
      line-height: 16px;
    }
    .item {
      padding: 4px 8px;
      .wrap {
        transition: all 0.3s;
        padding: 0 8px;
        border-radius: 4px;
        cursor: pointer;
        display: flex;
        line-height: 36px;
        align-items: center;
        justify-content: space-between;
        font-size: 14px;
        font-weight: 600;
        color: rgba(0,0,0,0.65);
        .name {
          display: flex;
          align-items: center;
          flex: 1;
          .icon {
            display: flex;
            background-color: #2f54eb;
            font-size: 12px;
            color: #fff;
            width: 16px;
            height: 16px;
            justify-content: center;
            align-items: center;
            border-radius: 3px;
            margin-right: 8px;
          }
        }
        .actions {
          opacity: 0;
          transition: all 0.3s;
        }
        &:hover {
          background-color: rgba(0,0,0,0.06);
          .actions {
            opacity: 1;
          }
        }
      }
      &.select {
        .wrap {
          background-color: #fff;
        }
      }
    }
  }
  .footer {
    display: none;
  }
}
