.container {
  display: flex;
  flex-direction: column;
  position: relative;
  height: 100%;
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 24px;
  user-select: none;
}
.content {
  flex: 1;
  overflow-y: auto;
  padding: 0 24px;
}
.list {
  margin: 0 -12px;
  display: flex;
  flex-wrap: wrap;
}
.pagination {
  text-align: center;
  padding: 8px 0 24px 0;
  width: 100%;
}
.uploadBar {
  position: absolute;
  bottom: 16px;
  left: 50%;
  transform: translateX(-50%);
}
