.main {
  display: flex;
  height: 100%;
  width: 100%;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.layout {
  display: flex;
  width: 100%;
  .left {
    width: 256px;
    height: 100%;
    border-right: 1px solid rgba(0,0,0,0.06);
    background-color: rgb(248, 248, 248);
  }
  .right {
    width: 100%;
    height: 100%;
    flex: 1;
  }
}

