.container {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  .panel {
    padding: 16px 16px 72px 16px;
    display: flex;
    flex-direction: column;
    align-items: center;
    user-select: none;

    .icon {
      width: 108px;
      height: 108px;
      font-size: 72px;
      border-radius: 50%;
      background: #d6e4ff;
      color: #597ef7;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .title {
      margin-top: 16px;
      font-size: 14px;
      color: #1b2733;
      text-align: center;
    }

    .desc {
      margin-top: 12px;
      font-size: 12px;
      line-height: 24px;
      color: #637282;
      text-align: center;
      .upload {
        color: #2f54eb;
        cursor: pointer;
        padding: 0 4px;
        font-size: 12px;
        &:hover {
          color: #597ef7;
        }
        &:active {
          color: #1d39c4;
        }
      }
    }
  }
}
