.view {
  width: 100%;
  height: 100%;
  img {
    width: 100%;
    height: 100%;
    object-fit: scale-down;
    background-image: linear-gradient(45deg, rgb(238, 238, 238) 25%, transparent 25%, transparent 75%, rgb(238, 238, 238) 75%, rgb(238, 238, 238)), linear-gradient(45deg, rgb(238, 238, 238) 26%, rgb(255, 255, 255) 26%, rgb(255, 255, 255) 74%, rgb(238, 238, 238) 74%, rgb(238, 238, 238));
    background-size: 20px 20px;
    background-position: 0px 0px, 10px 10px;
  }
  .thumbImage {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
