.container {
  background-color: #f5f5f5;
  border: 2px solid #f5f5f5;
  color: rgba(0, 0, 0, 0.65);
  border-radius: 2px;
  transition: all 0.2s;
  padding: 2px;
  overflow: hidden;
  position: relative;
  cursor: pointer;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  &:hover {
    background-color: #e7e7e7;
    .bell {
      animation-name: emphasize;
      animation-duration: 0.75s;
      animation-fill-mode: forwards;
      // animation-iteration-count: infinite;
    }
  }
  :global {
    .ant-progress {
      position: absolute;
      bottom: 0;
      margin: 0;
      line-height: 2px;
      font-size: 0;
      left: 0px;
    }
  }
}
.popover {
  :global {
    .ant-popover-title {
      padding: 0 4px;
      font-size: 14px;
    }
  }
}

.view {
  max-height: 320px;
  overflow-y: auto;
  margin: 0 -12px;
  padding: 0 16px;
  width: 420px;
  .item {
    padding: 12px 0;
    position: relative;
    border-bottom: 1px solid rgba(0,0,0,.1);
    width: 100%;
    color: rgba(0, 0, 0, 0.65);
    justify-content: space-between;
    line-height: 24px;
    &:last-child {
      border-bottom: none;
    }
  }
}

@keyframes emphasize{
  0%, 100%{
      transform: scale3d(1, 1, 1);
  }
  10%, 20%{
      transform: scale3d(0.9, 0.9, 0.9) rotate3d(0, 0, 1, -3deg);
  }
  30%, 50%, 70%, 90% {
      transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, 3deg);
  }
  40%, 60%, 80% {
      transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, -3deg);
  }
}
